import Swatch from '@/style/ts/Swatch';
function makeNewFavicon(color) {
    var id = 'dynamic-favicon';
    var tag = 'link';
    var rel = 'icon';
    var pathId = color.slice(1); // remove the # infront of the color
    var path = 'static/logo-' + pathId + '.png?v=2';
    var link = document.createElement(tag);
    link.rel = rel;
    link.href = path;
    swapHeadElementById(id, link);
}
function makeNewSelectionColor(color) {
    var id = 'dynamic-selection';
    var tag = 'style';
    var type = 'text/css';
    var style = document.createElement(tag);
    style.type = type;
    style.innerHTML =
        '::selection {' +
            'background:' + color + ';' +
            'color:' + Swatch.bright + ';' +
            '}' +
            '::-moz-selection {' +
            'background:' + color + ';' +
            'color:' + Swatch.bright + ';' +
            '}';
    swapHeadElementById(id, style);
}
function swapHeadElementById(id, elem) {
    var oldElem = document.getElementById(id);
    if (oldElem) {
        document.head.removeChild(oldElem);
    }
    elem.id = id;
    document.head.appendChild(elem);
}
export { makeNewFavicon, makeNewSelectionColor };



